.bg {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0.737789203084833) 0%,
    rgba(0, 0, 0, 0.9768637532133676) 45%
  );

  .container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 100vh;
    padding: 60px 20px 20px 20px;

    // &.main {
    //   background: no-repeat url("../../img/bg.png");
    //   background-size: cover;
    // }

    .img {
      width: 100%;
      height: 300px;

      img {
        height: auto;
        width: 100%;
        border-radius: 10px;
      }
    }

    &__icon {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;

      svg {
        width: 80px;
        height: 80px;
        color: white;
      }
    }

    &__main {
      width: 80%;

      .header {
        text-align: center;
        width: 100%;
        font-size: 30px;
        color: white;
      }

      .title {
        margin-top: 20px;
        text-align: center;
        width: 100%;
        font-size: 15px;
        color: white;
      }

      .description {
        margin-top: 20px;
        text-align: center;
        width: 100%;
        font-size: 15px;
        color: #0000009e;
      }
    }

    .buttons {
      height: 50px;
      width: 100%;

      button {
        height: 50px;
        background-color: white;
        margin-top: 5px;
        border-radius: 40px;

        span {
          color: black;
          font-size: 1rem;
        }
      }
    }

    .radio {
      width: 100%;

      .ant-radio-group {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;

        .ant-radio {
          display: none;
        }

        label {
          background-color: black;
          padding: 10px;
          border-radius: 10px;

          :last-child {
            color: white;
          }

          &:hover {
            opacity: 0.5;
          }
        }
      }
    }
  }
}
